import { Box, Flex, Image,Text } from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'

import axios from "axios"

import homePic from "../PortfolioHome.png"

import Typical from 'react-typical'

function Home() {

  const [allData,setAllData]= useState([]);
  
  const [loading,setLoading] = useState(false)

  const getData = async () => {
    setLoading(true)
    await axios.get(`https://api.github.com/users/kiranwankhade`).then((res)=>{
        // console.log(res.data.items);
        setAllData(res.data)
        setLoading(false)
    }).catch((err)=>{
        console.log(err)
        setLoading(false)
    })
  }



  useEffect(()=>{
    getData()
  },[])
    
  console.log("allData",allData)

  return loading ? <h1>Loading...</h1> : (
    <Box id='home' position={'absolute'} height={'100vh'} w='100%' marginTop={'50px'}
    //  bg='#4a077c'
    backgroundImage="linear-gradient(to right top, #0c978b, #0d826e, #106e53, #125a3a, #114723, #0b4525, #044227, #004029, #004d44, #005a60, #00657d, #006f96)"
     >
        <Box width={'90%'} margin='auto' display='flex' gap={'10px'} justifyContent='space-evenly' marginTop={'20px'} color='yellow'>
            <Flex gap={5} flex={4} width='100%' margin='auto' padding={5}>
                <Image src={allData.avatar_url} alt="home" width={250} height={250} borderRadius='15px' border={'1px dashed teal'}/>
                <Text style={{
                  fontSize:'25px',
                  fontWeight:'bold',
                  color:'teal'
                }}></Text>
                <Box 
                  fontSize='26px'>
                <Typical
                  steps={['Hello ', 1000, 'I am Kiran Wankhade', 500]}
                  loop={Infinity}
                  wrapper="p"
                />
                </Box>
            </Flex>
            <Box  flex={6}>
                <Image src={homePic} alt="home"/>
            </Box>
            
        </Box>
     
      <Flex color='yellow'>
      
        <Box alignItems={'left'} fontSize='20px' textAlign={'center'}>Web Developer with 1000+ hours of coding experience, passion for building applications, with proficiency in managing development projects. Hands-on experience in various programming languages.Experienced in creating apps in individual and collaborative environments. Looking forward to making a significant contribution to an organization through dedicated effort.</Box>
      </Flex>
      
    </Box>
  )
}

export default Home